import counter from '../../../components/counter.vue'
export default {

	props: [ "spaceInfo", "goodsInfo", "goodsSpec"],
	data() {
		return {
			isShow: '',
			data: {
				color_name : '',
				goods_id : '',
				id : '',
				kucun : '',
				price_one : '',
				thumbimg : ''
			} //选中商品的信息
		}
	},
	methods: {

		hideSpec() {
			this.$emit('func', this.data, false)
		},
		// getSelect(data) { //传入子组件的方法
		// 	this.count = data;
		// },
		setSelectSpec(item) { // 选择规格	
			this.data = item;
